}

.scroller::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #3bb3a9;
}

/* hidden scrollBar */
.not-scroller::-webkit-scrollbar {
  display: none;
}
