  .header-user {
    display: flex;
    align-items: center;
  }

  ul.header-user_ul.active {
    position: relative;
    display: block !important;
    left: 0;
    top: 0;
    box-shadow: none;
  }
  ul.header-user_ul.active li {
    width: 100%;
    padding: 0px !important;
  }
  .header-user:hover .header-user_ul {
    display: none;
  }
  .update-profile {
    padding: 0px 20px;
  }
  h1.edit-heading {
    color: #333;
    font-size: 26px;
    margin: 28px 0 -11px;
  }
  div#register-form {
    position: fixed;
    top: 7%;
    right: 3%;
    left: 3%;
    background: #fff;
    border-radius: 12px;
    padding: 15px 48px 15px;
    width: 94%;
    z-index: 999999;
    font-size: 14px;
  }
  body,
  html {
    overflow-x: hidden;
  }
  .main-header,
  .number-logo {
    display: none !important;
  }
  .mobile-menu {
    display: flex !important;
  }
  button.bars {
    color: #000;
    background: transparent;
    border: navajowhite;
    font-size: 26px;
  }

  .main-logo img {
    width: 150px;
  }
  .second_head_list ul.other-menu {
    position: absolute;
    width: 70%;
    background: #fff;
    height: 100vh;
    top: 0;
    transition: all 0.5s;
    transform: translateX(270px);
    flex-direction: column;
    z-index: 99999;
    right: 0;
    gap: 20px;
    display: block !important;
    padding: 20px;
  }
  .second_head_list.active ul {
    transform: none;
  }
  .other-menu li {
    padding: 10px 0px !important;
    display: block;
    font-weight: 500;
    border-bottom: 1px solid #dee2e6;
  }
  .other-menu img {
    margin-right: 5px;
    margin-left: 0px;
  }
  .mobile-sec-img img {
    margin-left: -13px !important;
  }
  .mobile-menu-head {
    display: flex !important;
  }
  input.search_input {
    padding: 9px 12px !important;
  }
  .search-btn button.btn-sm {
    padding: 9px;
  }
  .search_form {
    margin: 5px 0px 10px;
  }
  .main-top-product .col-sm-2 {
    width: 50%;
    margin: 10px 0px;
  }
  #masthead {
    padding: 0px 12px;
    margin: 0px 0px 10px;
    box-shadow: 0px 0px 8px #adb5bdc4;
  }
  button.slick-arrow.slick-prev {
    display: none !important;
  }
  button.slick-arrow.slick-next {
    display: none !important;
  }
  .wearTrend {
    font-size: 30px !important;
    text-align: center;
  }
  h4.banner-heading {
    letter-spacing: 0.5px;
    font-size: 18px;
    margin-top: 20px;
  }
  .first-sec h1 {
    font-size: 19px;
    line-height: 32px;
  }
  .first-sec {
    display: none;
  }
  .second-sec .col-md-4 {
    width: 50%;
  }
  .second-sec {
    padding: 17px 34px 35px;
  }
  .third-sec,
  .footer-line {
    display: none !important;
  }
  .trending-prdt-explore {
    width: 90%;
  }
  .all-products .col-sm-4.p-0 {
    width: 50%;
  }
  .all-products .product-img {
    width: 100%;
    height: inherit;
  }
  .sortby .d-flex {
    padding: 6px 22px;
  }
  .sidebar-category {
    box-shadow: 0px 0px 6px #00000052;
    margin: 14px;
    padding: 20px;
    border-radius: 10px;
  }
  .filter-col {
    height: auto;
  }
  .prdt-name,
  .cart-price {
    font-size: 12px;
  }
  .main-cart .col-sm-3 {
    box-shadow: 0px 0px 6px #00000052;
    padding: 20px;
    border-radius: 10px;
  }
  .cart-page {
    padding: 0px;
  }
  .cart-card {
    margin: 10px 0px !important;
    border-radius: 10px;
  }
}
